@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");

@font-face {
  font-family: "Futura PT";
  src: url("../public/fonts/FuturaPT.woff") format("woff");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: "Modure";
  src: url("../public/fonts/Modure.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  line-height: 1.6;
  font-size: 16px;
  font-style: normal;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

.glow-button {
  --button-background: rgb(25, 25, 31);
  --button-color: #e0e0e0;
  --button-shine-left: rgba(255, 255, 255, 0.3);
  --button-shine-right: rgba(128, 128, 128, 0.65);
  --button-glow-start: #c8c8c8;
  --button-glow-end: #4a4a4a;
  --button-gradient-color: rgba(255, 255, 255, 0.83);

  appearance: none;
  outline: none;
  border: none;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  border-radius: 11px;
  position: relative;
  cursor: pointer;
  color: var(--button-color);
  padding: 0;
  margin: 0;
  background: none;
  z-index: 1;
}

.gradient {
  position: absolute;
  inset: 0;
  border-radius: inherit;
  overflow: hidden;
  transform: scale(1.04);
}

.gradient:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  animation: rotate 5s linear infinite;
  background: conic-gradient(
    from 0deg at 50% 50%,
    rgba(255, 255, 255, 0) 0deg,
    rgba(217, 217, 217, 0) 176.757deg,
    var(--button-gradient-color) 290.784deg,
    rgba(194, 194, 194, 0) 274.054deg,
    rgba(171, 171, 171, 0) 360deg
  );
  border-radius: 100px;
  filter: blur(10px);
  transform-origin: 50% 50% 0px;
}

span {
  z-index: 1;
  position: relative;
  display: flex;
  box-sizing: border-box;
  width: fit-content;
  border-radius: inherit;
  background-color: var(--button-background);
  overflow: hidden;
  mask-image: -webkit-radial-gradient(white, black);
}

span:before {
  content: "";
  position: absolute;
  left: -16px;
  top: -16px;
  transform: translate(var(--pointer-x, 0px), var(--pointer-y, 0px))
    translateZ(0);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: var(--button-glow, transparent);
  opacity: var(--button-glow-opacity, 0);
  transition: opacity var(--button-glow-duration, 0.5s);
  filter: blur(20px);
}

.glow-button:hover span:before {
  --button-glow-opacity: 1;
  --button-glow-duration: 0.35s;
}

@keyframes rotate {
  to {
    transform: translateX(0px) translateY(0px) scale(1) rotate(360deg) 
      rotateX(0deg) rotateY(0deg) translateZ(0px);
  }
}

@media (max-width: 600px) {
  .container {
    padding: 0 16px;
  }
}

@media (min-width: 600px) {
  .container {
    padding: 0 24px;
  }
}
